import React, { useCallback, useState } from "react";
import "./style.css";
import panel1bg from "../../assets/mode-backgr.png";
import Carousel from '../../package/react-grid-carousel/src/app';
import modeCard1 from "../../assets/mode-card1.png";
import modeCard2 from "../../assets/mode-card2.png";
import modeCard3 from "../../assets/mode-card3.png";
import arrorL from "../../assets/arrorL.png";
import arrorR from "../../assets/arrorR.png";
import styled from 'styled-components'


const CustomBtn = styled.div`
  position: absolute;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 40px;
  width: 40px;
  font-size: 20px;
  color: red;
  opacity: 0.6;
  cursor: pointer;
  top: 50%;
  z-index: 10;
  transition: all 0.25s;
  transform: ${({ type }) =>
    `translateY(-50%) ${type === 'left' ? 'rotate(180deg)' : ''}`};
  left: ${({ type }) => (type === 'left' ? '20px' : 'initial')};
  right: ${({ type }) => (type === 'right' ? '20px' : 'initial')};
  &:hover {
    color: #fff;
    opacity: 0.5;
  }
`
export default function Mode() {
    const [isHover, setIshover] = useState(false)

    const handleHover = useCallback(() => {
        setIshover(state => !state)
    }, [])
    return (
        <div className="font-face-gm modepage">
            <div class="panel1">
                <img src={panel1bg} alt="panel1bg" />
            </div>
            <div className="panel2" >
                <Carousel 
                arrowLeft={<CustomBtn type="left"> <img src={arrorR}  ></img></CustomBtn>}
                arrowRight={<CustomBtn type="right"><img src={arrorR}  ></img></CustomBtn>}
                cols={3} rows={1} gap={10} loop autoplay={30000}  >
                    <Carousel.Item>
                        <div className="mode-parent">
                            <img width="100%" src={modeCard1} />
                            <div className="card-content">
                                <div>Boss</div>
                                <div>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Praesentium quia quasi, debitis odio laborum molestias dolore ut officiis, exercitationem vel iure dolorum sint quidem corrupti assumenda eius ex ab quaerat.</div>
                            </div>
                        </div>
                    </Carousel.Item>
                    <Carousel.Item>
                    <div className="mode-parent">
                            <img width="100%" src={modeCard2} />
                            <div className="card-content">
                                <div>Boss</div>
                                <div>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Praesentium quia quasi, debitis odio laborum molestias dolore ut officiis, exercitationem vel iure dolorum sint quidem corrupti assumenda eius ex ab quaerat.</div>
                            </div>
                        </div>
                    </Carousel.Item>
                    <Carousel.Item>
                    <div className="mode-parent">
                            <img width="100%" src={modeCard3} />
                            <div className="card-content">
                                <div>Boss</div>
                                <div>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Praesentium quia quasi, debitis odio laborum molestias dolore ut officiis, exercitationem vel iure dolorum sint quidem corrupti assumenda eius ex ab quaerat.</div>
                            </div>
                        </div>
                    </Carousel.Item>
                    <Carousel.Item>
                    <div className="mode-parent">
                            <img width="100%" src={modeCard1} />
                            <div className="card-content">
                                <div>Boss</div>
                                <div>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Praesentium quia quasi, debitis odio laborum molestias dolore ut officiis, exercitationem vel iure dolorum sint quidem corrupti assumenda eius ex ab quaerat.</div>
                            </div>
                        </div>
                    </Carousel.Item>
                    
                </Carousel>
            </div>
        </div>
    );
}