import { Col, Layout, Menu, Row } from 'antd';
import { InfoCircleOutlined } from '@ant-design/icons';

import Icon, { DoubleRightOutlined, MenuFoldOutlined, MenuUnfoldOutlined } from '@ant-design/icons';
import logo from "../../../assets/Logo_v2.png";
import './assets/css/index.css';
import { useEffect, useState } from 'react';
import { Link, useLocation } from 'react-router-dom';

import Connect from "../../../assets/connect.svg";
import { AuthButton, ModalError, SimpleButton } from "../../../components/Button/Button";
import ModalConnect from "../../../components/ModalConnect/ModalConnect";
import { YourRewardPage10 } from "../..";
import { isMobile } from 'react-device-detect';
import { useWeb3React } from "@web3-react/core";

import {
    useStoreApi,
    getBalance,
    getBNBBalance,
    fetchAccountAllowance,
  } from "../../../services";
const { Header, Content, Footer, Sider } = Layout;

const Header_ = ({ headerHeight }) => {
    const [collapsed, setCollapsed] = useState(false);
    const location = useLocation();

    const [openModal, setOpenModal] = useState(false);

    const {
        library,
        chainId,
        account,
        activate,
        deactivate,
        active
      } = useWeb3React();
      const {
        address,
        balance,
        setBalance,
        setAddress,
        setAllowance,
        setEWB,
        EWB,
      } = useStoreApi();
    const onCollapse = () => {
        setCollapsed(!collapsed);
    }

    useEffect(() => {
       setUserAccount();
    }, [account]);
    const setUserAccount = async () => {
        if (window.ethereum) {
          if(account) {
            console.log(account);
            setAddress(account);
            setUserBalanceBNB(account);
            setUserBalanceEMB(account);
            let allowance = await fetchAccountAllowance(account);
            if (allowance < 1000000) {
              setAllowance(false);
            } else {
              setAllowance(true);
            }
          };
        }
      };
      const setUserBalanceBNB = async (fromAddress) => {
        let balanceBNB = await getBNBBalance(fromAddress);
        setBalance(balanceBNB);
      };
      const setUserBalanceEMB = async (fromAddress) => {
        let balanceEMB = await getBalance(fromAddress);
        setEWB(balanceEMB);
      };
      const handleOpenModal = () => {
        setOpenModal(true);
      };
      const [reward, setReward] = useState(false);
    
    

    const items = [
        {
            label: (
                <Link to="/">
                    Home
                </Link>
            ), key: 'HOME'
        },
        {
            label: (
                <Link to="soul">
                    Soul
                </Link>
            ), key: 'SOUL'
        },
        {
            label: (
                <Link to="sword">
                    Sword
                </Link>
            ), key: 'SWORD'
        },
        // {
        //     label: (
        //         <Link to="pvepage">
        //             Boss
        //         </Link>
        //     ), key: 'BOSS'
        // },
        // //battle
        // {
        //     label: (
        //         <Link to="pvppage">
        //             Battle
        //         </Link>
        //     ), key: 'BATTLE'
        // },
        {
            label: (
                <Link to="mode">
                    Mode
                </Link>
            ), key: 'MODE'
        },
        {
            label:
                "Blacksmith"
            , key: 'BLACKSMITH',
            children: [
                {
                    label: <Link to="Awake">
                        Awake
                    </Link>,
                    key: 'Awake',
                },
                {
                    label: <Link to="Enchant">
                        Enchant
                    </Link>,
                    key: 'Enchant',
                },
                {
                    label: <Link to="Salvage">
                        Salvage
                    </Link>,
                    key: 'Salvage',
                },
                {
                    label: <Link to="Repair">
                        Repair
                    </Link>,
                    key: 'Repair',
                }
            ],
        },
        //stacking
        // {
        //     label: (
        //         <Link to="stakingpage">
        //             Stacking
        //         </Link>
        //     ), key: 'STACKING'
        // },
        {
            label: (
                <Link to="market">
                    Market
                </Link>
            ), key: 'MARKET'
        },
        {
            label: (
                <Link to="cardpage">
                    My storage
                </Link>
            ), key: 'STORAGE'
        }

        
    ];

    const openKeys = () => {
        let openKey = '';
        if (location?.pathname?.includes("pvepage")) openKey = "BOSS";
        if (location?.pathname?.includes("pvppage")) openKey = "BATTLE";
        if (location?.pathname?.includes("mode")) openKey = "MODE";
        if (location?.pathname?.includes("staking")) openKey = "STACKING";
        if (location?.pathname?.includes("upgrade")) openKey = "BLACKSMITH";
        if (location?.pathname?.includes("Awake")) openKey = "BLACKSMITH";
        if (location?.pathname?.includes("Enchant")) openKey = "BLACKSMITH";
        if (location?.pathname?.includes("Salvage")) openKey = "BLACKSMITH";
        if (location?.pathname?.includes("Repair")) openKey = "BLACKSMITH";
        if (location?.pathname?.includes("market")) openKey = "MARKET";
        if (location?.pathname?.includes("cardpage")) openKey = "STORAGE";
        //sword
        if (location?.pathname?.includes("sword")) openKey = "SWORD";
        return [openKey];
    }

    const selectedKeys = () => {
        console.log(location?.pathname)
        let activeKey = 'HOME';
        if (location?.pathname?.includes("pvepage")) activeKey = "BOSS";
        else if (location?.pathname?.includes("pvppage")) activeKey = "BATTLE";
        else if (location?.pathname?.includes("soul")) activeKey = "SOUL";
        else if (location?.pathname?.includes("mode")) activeKey = "MODE";
        else if (location?.pathname?.includes("staking")) activeKey = "STACKING";
        else if (location?.pathname?.includes("upgrade")) activeKey = "BLACKSMITH";
        else if (location?.pathname?.includes("Awake")) activeKey = "Awake";
        else if (location?.pathname?.includes("Enchant")) activeKey = "Enchant";
        else if (location?.pathname?.includes("Salvage")) activeKey = "Salvage";
        else if (location?.pathname?.includes("Repair")) activeKey = "Repair";
        else if (location?.pathname?.includes("market")) activeKey = "MARKET";
        else if (location?.pathname?.includes("cardpage")) activeKey = "STORAGE";
        //sword
        else if (location?.pathname?.includes("sword")) activeKey = "SWORD";
        return [activeKey];
    }

    return (
        <>
        <Header className='header'>
        <div>

            <div className="logo">
                <img src={logo} alt="logo" style={{ width: 100 }} />
            </div>
            <Menu selectedKeys={selectedKeys()} defaultOpenKeys={openKeys()} items={items} mode="horizontal" style={{ width: '60%' }} />

            <div className='connect-wl' >
                <div className='myreward-btn'>
                    My reward
                </div>
                <div className='connect-btn' onClick={() => {
                    handleOpenModal();
                    if (account) {
                      setUserAccount();
                    }
                  }}>
                    {
                    account
                      ? `${account.substring(0, 4)}...${account.substring(
                        account.length - 3,
                        account.length
                      )}`
                      : "Connect wallet"
                  }
                </div>
            </div>
            </div>

        </Header >
        {openModal ? <ModalConnect setOpen={setOpenModal} /> : null}

        </>
    )
}

export default Header_;

